'use strict';

/**
* Return a regular expression to capture a POSIX path dirname.
*
* @module @stdlib/regexp/dirname-posix
*
* @example
* import reDirnamePosix from '@stdlib/regexp/dirname-posix';
*
* var RE_DIRNAME_POSIX = reDirnamePosix();
* var dir = RE_DIRNAME_POSIX.exec( 'foo/bar/index.js' )[ 1 ];
* // returns 'foo/bar'
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import REGEXP from './regexp.js';


// MAIN //

setReadOnly( main, 'REGEXP', REGEXP );


// EXPORTS //

export default main;
