'use strict';

/**
* Regular expression to match a duration string.
*
* @module @stdlib/regexp/duration-string
*
* @example
* import reDurationString from '@stdlib/regexp/duration-string';
*
* var RE_DURATION = reDurationString();
* // returns <RegExp>
*
* var parts = RE_DURATION.exec( '3d2ms' );
* // returns [...]
*
* parts = RE_DURATION.exec( '2ms' );
* // returns [...]
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-nonenumerable-read-only-property';
import main from './main.js';
import REGEXP from './regexp.js';


// MAIN //

setReadOnly( main, 'REGEXP', REGEXP );


// EXPORTS //

export default main;
